export const createGig = async (gigData: any) => {
  const {
    title,
    description,
    price,
    category,
    images,
    tags,
    packages,
    userId,
    userName,
    userAvatar,
  } = gigData;

  // Ensure user exists in users table
  const { data: existingUser } = await supabase
    .from("users")
    .select("*")
    .eq("id", userId)
    .single();

  if (!existingUser) {
    await supabase
      .from("users")
      .insert({ id: userId, name: userName, email: "", avatar: userAvatar });
  }

  const newGig = {
    id: Date.now().toString(),
    user_id: userId,
    title,
    description,
    price,
    category,
    images: images || [],
    tags: tags || [],
    packages: packages || [],
    rating: 0,
    reviews: 0,
  };
  const { data, error } = await supabase
    .from("gigs")
    .insert(newGig)
    .select()
    .single();

  if (error) throw error;
  return data;
};

export const updateGig = async (gigId: string, updates: any) => {
  const { data, error } = await supabase
    .from("gigs")
    .update(updates)
    .eq("id", gigId)
    .select()
    .single();

  if (error) throw error;
  return data;
};
